.Receive {
  text-align: center;
  width: 100%;
  border: 1px solid green;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.message {
  font-family: "Montserrat", sans-serif;
  font-size: large;
  line-height: 16px;
}
.messageBox {
  height: 20px;
  display: flex;
  justify-content: center;
  width: 100%;
  align-items: center;
  margin: 50px 0px;
}
.btn{
  font-family: Roboto, sans-serif;
  font-weight: 0;
  font-size: 14px;
  color: #fff;
  background-color: #2a9d8f;
  padding: 10px 30px;
  border: solid #000000 2px;
  box-shadow: rgb(0, 0, 0) 0px 0px 0px 0px;
  border-radius: 6px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  }
  
